.App {
  text-align: center;
}

.zstack {
	display: grid;
	align-items: center;
	justify-items: center;
}

.zstack > * {
	grid-area: 1/1/1/1;
}


.App-logo {
  height: 40vmin;
  pointer-events: snone;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.nav-link-active, .link {
  color: #2C6030 !important;
}

.nav-link-inactive {
  color: #2c603075 !important;
}

.r90{
  transform: rotate(90deg);
}



.vr{
  color: #2C6030 !important;
  width: 1.60px;
}

.opac{
  opacity: 1 !important;
}
.zic{
  color: #2C6030 !important;
  /* opacity: 0.3; */
  margin-bottom: -1px;
  margin-top: -1px;
}

.ml-100{
  margin-left: 150px;
  width: 100px;
  text-align: left;
}

.mb-50{
  margin-bottom: 50px;
}

.version-number{
  font-size: 15px;
  opacity: 0.5;
  font-weight: bolder;
}

.top-icon{
  width: 120px  !important;
}

.feature-img{
  max-width: 200px !important;
}
.feature-img:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}
@media only screen and (min-width: 768px) {
  .feature-img {
    max-width: 250px !important;
  }
  .top-icon{
    width: 300px !important;
  }
}